<template>
	<div class="store_header">
		<div class="store_avatar">
			<!-- <img src="../../assets/images/avatar_default.png" alt="头像" width="55" height="55"> -->
			<div class="logo-container">
				<img class="logo" src="../../assets/images/logo.png" />
			</div>
		</div>
	</div>
</template>


<script>
	export default {
		name: "login-header"
	}
</script>

<style lang="scss" scoped>
	.store_header{
		text-align: center;
		padding: 50px 0;
		.store_avatar img{
			border-radius: 50%;
		}
		.store_name{
			padding-top: 5px;
			font-size: 16px;
		}

		.logo-container {
			width: 160px;
			height: 160px;
			margin: 0 auto;
			border: 3px solid rgba($color:#fff, $alpha: 0.4);
			border-radius: 50%;
		}

		.logo {
			width: 100%;
			height: 100%;
			box-sizing: border-box;
			padding: 5px;
		}
	}
</style>



