<template>
	<div>
		<md-field-group>
			<md-field
				v-model="account"
				icon="username"
				right-icon="clear-full"
        placeholder="手机号"
				@right-click="clearText" />

			<md-field
				v-model="password"
				icon="lock"
				:type="visiblePass ? 'text' : 'password'"
				:right-icon="visiblePass ? 'eye-open' : 'eye-close'"
        placeholder="登陆密码"
				@right-click="visiblePass = !visiblePass" />
			<van-button size="large" type="danger" :loading="isLogining" @click="loginSubmit">登录</van-button>
		</md-field-group>
	</div>
</template>

<script>
	import field from '@/vue/components/field/';
	import fieldGroup from '@/vue/components/field-group/';
  import {Toast} from 'vant';

	import {
		USER_LOGIN
	} from '@/api/user';

	export default {
		name: "login-request",

		data() {
			return {
				account: "18259206866",
				password: "123456",
				visiblePass: false,
				showKefu: false,
				isLogining: false,
			}
		},

		methods: {
			clearText() {
				this.account = "";
			},

      loginSubmit() {
				console.log(this.account, this.password)
        // if(!isMobile(this.account)){
        //   Toast('请填写正确的手机号码');
        //   return;
        // }
        if(!this.password){
          Toast('请填写密码');
          return;
        }
        this.isLogining = true;
        this.$reqPost(USER_LOGIN, {
          account : this.account,
          password: this.password
        }).then((res) => {
          this.loginSuccess(res);
        }).catch((err) => {
          this.isLogining = false;
        })
			},
      loginSuccess(res){
        let result = res.data;
        if(result.code!=0){
          this.isLogining = false;
          Toast(result.msg);
          return;
        }
				localStorage.clear();
				this.isLogining = false;
				const localData = res.data.data;
				const redirect = this.$route.query.redirect || 'index';
				localStorage.token=localData.token;
				localStorage.account=localData.account;
        localStorage.name=localData.name;
        localStorage.first_leader=localData.first_leader;
        localStorage.superior_id=localData.superior_id;
        localStorage.superior_name=localData.superior_name;
				localStorage.invite_auth=localData.invite_auth;
        this.$router.replace({
          name: redirect,
          query: this.$route.query
				})
      }
		},
		components: {
			[field.name]: field,
			[fieldGroup.name]: fieldGroup,
      [Toast.name] : Toast
		},

	}

</script>

<style lang="scss" scoped>

	@import "../../assets/scss/mixin";
	.register {
		color: $font-color-gray;
		a {
			color: $font-color-gray;
		}
		>div {
			width: 50%;
			box-sizing: border-box;
			padding: 0 20px;
		}
		.connect {
			@include one-border(right);
			text-align: right;
		}
	}
  .contact-box {margin-top:20px;}
  .contact-tip { padding:10px 20px; font-size:14px; color:#444;}
  .contact-box .c-item{
    float:left;
    width:50%;
    text-align:center;
  }
  .contact-box .c-item p {padding:0; margin:0;}
  .contact-box .c-item img{
    width:60%;
    height:60%;
  }

</style>



