<template>
	<div class="field_group">
		<slot></slot>
	</div>
</template>


<script>
	export default {
		name: 'md-field-group'
	}
</script>

<style lang="scss" scoped>
	.field_group {
		padding: 0 12vw;
		
		> div{
			margin-bottom: 15px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
</style>


